import React, { useState, useEffect } from 'react';
import './App.css';
import axios from "axios"
import * as XLSX from "xlsx";

// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: "AIzaSyBKGOMyHVPHaAwRmlod2np8YFwWqxO8T6M",
  authDomain: "vlh-tuyendung.firebaseapp.com",
  projectId: "vlh-tuyendung",
  storageBucket: "vlh-tuyendung.appspot.com",
  messagingSenderId: "736260500988",
  appId: "1:736260500988:web:6b40bc98304c39a3ee1679",
  measurementId: "G-D5H2SLLPLG",
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);

function App() {
  const [items, setItems] = useState([]);
  const [smsText, setSmsText] = useState("");
  // const [exportedData, setExportedData] = useState(false);

  useEffect(() => {
    fetchDataFromMongoDB();
    console.log(items);
  }, []);

  const fetchDataFromMongoDB = async () => {
    try {
      const response = await axios.get(
        "https://tuyendung-vlh.onrender.com/api/admin/users"
      );
      setItems(response.data);
      console.log(response.data);
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  };

  const renderTable = () => {
    return (
      <table>
        <thead>
          <tr>
            <th>#</th>
            <th>Ho va Tn</th>
            <th>Email</th>
            <th>S in thoai</th>
            <th>Ngay Sinh</th>
            <th>Gii tinh</th>
            <th>Cn cc cng nhn</th>
            <th>Trinh  hoc vn</th>
            <th>Vi tri ng tuyn</th>
          </tr>
        </thead>
        <tbody>
          {items.slice(0).map((item, index) => (
            <tr key={index}>
              <td>{index + 1}</td>
              <td>{item.fullname}</td>
              <td>{item.email}</td>
              <td>{item.numberPhone}</td>
              <td>{item.date}</td>
              <td>{item.gender}</td>
              <td>{item.cccd}</td>
              <td>{item.tdhv}</td>
              <td>{item.vtut}</td>
              <textarea
                id={item._id}
                value={smsText}
                onChange={(e) => setSmsText(e.target.value)}
                required
              />
              <br />
              <button
                value={item.numberPhone}
                onClick={() => handleSendSMS(item.numberPhone)}
              >
                Gi SMS
              </button>
            </tr>
          ))}
        </tbody>
      </table>
    );
  };

  const exportToExcel = () => {
    console.log(items);
    const wb = XLSX.utils.book_new();
    const header = [
      // "Ho va Tn",
      // "Email",
      // "S in thoai",
      // "Ngay sinh",
      // "Gii tinh",
      // "CCCD",
      // "Trinh  hoc vn",
      // "Vi tri ng tuyn",
    ];
    const ws = XLSX.utils.aoa_to_sheet([[header]]);
    XLSX.utils.sheet_add_json(ws, items, { header });
    XLSX.utils.book_append_sheet(wb, ws, "Sheet1");
    XLSX.writeFile(wb, "mongodb_data.xlsx");
  };

  const deleteData = (id) => {
    console.log(id);
    try {
      axios.delete(`https://tuyendung-vlh.onrender.com/api/data/${id}`);
      console.log("Data deleted successfully");
    } catch (error) {
      console.error("Error deleting data:", error);
    }
  };

  const handleSendSMS = (numberPhone) => {
    // Gi yu cu gi tin nhn ti backend
    axios
      .post("https://tuyendung-vlh.onrender.com/api/admin/sendSMS", {
        numberPhone,
        smsText,
      })
      .then((response) => {
        console.log(` sdt nhn: ${numberPhone}`);
        console.log(response.data);
        setSmsText();
        alert("Tin nhn  c gi!");
      })
      .catch((error) => {
        console.error(" c li xy ra khi gi tin nhn:", error);
        alert(" c li xy ra khi gi tin nhn!");
      });
  };

  return (
    <div className="container">
      <h1>Danh sach ngi dung</h1>
      <button onClick={exportToExcel}>Export to Excel</button>
      {/* {renderTable()} */}
      <ul>
        {items.map((item) => (
          <li key={item._id}>
            <h2>Ho va tn: {item.fullname}</h2>
            <p>Email: {item.email}</p>
            <p>S in thoai: {item.numberPhone}</p>
            <p>Ngay sinh:{item.date}</p>
            <p>Gii tinh: {item.gender}</p>
            <p>Cn cc cng dn: {item.cccd}</p>
            <p>Vi tri ng tuyn: {item.vtut}</p>
            <p>Ni dung SMS</p>
            <textarea
              id={item._id}
              value={smsText}
              onChange={(e) => setSmsText(e.target.value)}
              required
            />
            <br />
            <button
              value={item.numberPhone}
              onClick={() => handleSendSMS(item.numberPhone)}
            >
              Gi SMS
            </button>
            {/* <img src={item.image} alt={item.fullname} /> */}
            <button onClick={() => deleteData(item._id)}>Xoa</button>
          </li>
        ))}
      </ul>
      ;
    </div>
  );
}

export default App;







